
import request from '@/utils/request'





/*============Home  start============*/
/**
 * @description: 首页话题列表信息
 * @param {type}
 * @return:
 */
export function indexlist(params) {
  return request({
    url: '/subject/index/list',
    method: 'get',
    params:params
  })
}
/**
 * @description: 查找话题列表信息
 * @param {type}
 * @return:
 */
export function subjectfind(params) {
  return request({
    url: '/user/subject/find',
    method: 'post',
    data:params
  })
}
/*============Home end============*/
