<template>
    <div>
         <breadcrumb-wrapper>
            <template v-slot:left>
                <el-breadcrumb class="app-breadcrumb" separator="/" >
                    <transition-group name="breadcrumb">
                    <el-breadcrumb-item key="1" v-if="fromPage=='Database'">
                        <router-link  :to="'/database?id='+detail.subject_id">资料库</router-link>
                    </el-breadcrumb-item>
                    <el-breadcrumb-item key="2" v-if="fromPage=='DataList'">
                        <router-link  to="/dataList">我的资料</router-link>
                    </el-breadcrumb-item>
                    <el-breadcrumb-item key="3" v-if="fromPage=='ArticleList'">
                        <router-link  to="/articleList">我的文章</router-link>
                    </el-breadcrumb-item>
                    <el-breadcrumb-item key="4" v-if="fromPage=='Studio'">
                        <router-link  :to="'/studio?id='+detail.subject_id">{{detail.subject_title}}工作室</router-link>
                    </el-breadcrumb-item>
                    <el-breadcrumb-item key="5" v-if="fromPage=='Article'">
                        <router-link  :to="'/article?id='+detail.subject_id+'&uid='+uid">我添加的文章</router-link>
                    </el-breadcrumb-item>
                    <el-breadcrumb-item key="5" v-if="fromPage=='Workbench'">
                        <router-link to="/workbench">我的工作台</router-link>
                    </el-breadcrumb-item>

                    <el-breadcrumb-item key="6">
                        <span v-if="name=='ArticleDetail'">文章详情</span>
                        <span v-if="name=='DataDetail'">资料详情</span>
                    </el-breadcrumb-item>
                    </transition-group>
                </el-breadcrumb>
            </template>
        </breadcrumb-wrapper>
         <el-card shadow='hover' body-style="padding:10px 30px 30px;" >
            <div class="article-header w750">
                     <div class="flexBox" >
                         <div class="flexItem">
                            <h2 class="ellipsis" >
                                {{detail.title}}
                            </h2>
                         </div>
                         <div>
                             <template v-if="detail.role=='owner'">
                                 <router-link :to="'/editArticle/'+detail.id+'?sid='+detail.subject_id+'&fromPage=ArticleDetail'" v-if="name=='ArticleDetail'">
                                    <el-button type="text" icon="el-icon-edit">编辑</el-button>
                                </router-link>
                                <router-link :to="'/editData/'+detail.id+'?sid='+detail.subject_id+'&fromPage=DataDetail'" v-if="name=='DataDetail'">
                                    <el-button type="text" icon="el-icon-edit">编辑</el-button>
                                </router-link>
                             </template>
                         </div>
                     </div>

                    <div class="flexBox" style="align-items:center">
                        <div style="margin-right:10px;">
                            <avatar :name='detail.creator.user_abbr' :headIcon = 'detail.creator.user_headicon' :bgColor='detail.creator.user_backcolor'  size='mini' />
                        </div>
                        <div class="flexItem">
                        <span style="color:#1890FF;">{{detail.creator.user_name}} </span>
                            <span class="fs12">发布于{{detail.ptime}}</span>
                        </div>
                        <div class="flexItem fs12" style="text-align:right">
                            最近更新：{{detail.mtime}}
                        </div>
                    </div>
            </div>
            <div class="article-content smeditor w750" v-html="detail.content">

            </div>
        </el-card>
    </div>

</template>
<script>
import Avatar from '@/components/Avatar'
import BreadcrumbWrapper from '@/layout/BreadcrumbWrapper'
import { subjectget } from '@/api/index'
export default {
    name: 'ArticleDetail',
    components: {
        Avatar,
        BreadcrumbWrapper
    },

    data() {
        return {
            detail:{
                creator:{
                    user_abbr:''
                }
            },
            name:'',
            fromPage:this.$route.query.fromPage,
            uid:this.$store.state.user.user.id,
        }
    },
    mounted() {
        const name = this.$route.name
        this.name = name

        if(name === 'ArticleDetail'){
            this.findArticleDetail()
        }

        if(name === 'DataDetail'){
            this.findMaterialDetail()
        }
    },
    methods: {
        findArticleDetail(){
            const { id } = this.$route.params
             this.$store.dispatch('article/articleGet',{id}).then((result)=>{
                 this.detail = result.data;

             })
        },
        findMaterialDetail(){
            const { id } = this.$route.params
             this.$store.dispatch('article/materialGet',{id}).then((result)=>{
                 this.detail = result.data;
             })
        }
    },
}
</script>
<style lang="scss" scoped>
 @import "~@/styles/variables.scss";
 .article-header {
     border-bottom: 1px solid $border-2;
     padding-bottom:20px
 }

 .smeditor /deep/ img {
     width: auto !important;
     height: auto !important;
     max-width: 100%
 }

 .smeditor {
     width:100%;
 }
 .fs12 {
     color: $placeholderText;
     font-size: 12px;
     margin-left: 10px;
 }

 .article-content {
     line-height: 1.8;
     margin-top:10px;
     min-height: 400px;
 }
</style>
